$text_color: red;
@mixin box {
    display: block;
    width: 250px;
    height: 200px;
    background-color: green; }

h1 {
    color: red;
    font-size: 50px;
    @include box; }
h2 {
    color: green; }
.block {
    background-color: blue;
    h3 {
         color: red; }
    &_color {
        background-color: blue;
        button {
            @include box; } } }
